















import Vue from 'vue';
import Component from 'vue-class-component';
import { Watch } from 'vue-property-decorator';
import { Socket } from 'vue-socket.io-extended';
import WithSpinner from '@/components/withSpinner/index.vue';

const Message = () => import('../Message/index.vue');
const ChatInput = () => import('../ChatInput/index.vue');

interface Message {
  message: string,
  timestamp: object,
  isBot: boolean
}

@Component({
  name: 'MessagingWindow',
  components: {
    Message,
    ChatInput,
    WithSpinner,
  },
})
export default class MessagingWindow extends Vue {
  messages: Message[] = [];

  isLoading = true;

  @Watch('$route.path')
  onRoutePathChanged() {
    this.isLoading = true;
  }

  get reversedMessages() {
    const messages = [...this.messages];
    return messages.reverse();
  }

  onSendMessage(message: Message) {
    this.messages.push(message);
  }

  @Socket('join_room')
  onJoinRoom(data: Message[]) {
    this.isLoading = true;
    this.messages = data;
    this.isLoading = false;
  }

  @Socket('message')
  onMessage(message: Message) {
    this.messages.push(message);
  }
}
